@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&family=JetBrains+Mono:wght@300;400;500;700&family=Poppins:wght@300;400;500;600;700;800;900&display=swap');

/* Font rendering optimization */
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  font-family: 'Inter', system-ui, sans-serif;
  font-feature-settings: 'kern' 1;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  @apply bg-cyber-silver-900;
}

::-webkit-scrollbar-thumb {
  @apply bg-aegis-blue-500 rounded-full;
}

::-webkit-scrollbar-thumb:hover {
  @apply bg-aegis-blue-400;
}

/* Cyber glow effects */
.cyber-glow {
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.2);
}

.cyber-glow:hover {
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.3);
}

/* Threat alert animation */
.threat-pulse {
  animation: threat-pulse 1s ease-in-out infinite;
}

@keyframes threat-pulse {

  0%,
  100% {
    opacity: 1;
    transform: scale(1);
  }

  50% {
    opacity: 0.7;
    transform: scale(1.05);
  }
}

/* MEV detection grid */
.detection-grid {
  background-image:
    radial-gradient(circle at 25% 25%, rgba(255, 255, 255, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(255, 255, 255, 0.05) 0%, transparent 50%);
}

/* Status indicators */
.status-protected {
  @apply bg-safe-green-500 shadow-lg;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
}

.status-vulnerable {
  @apply bg-threat-red-500 shadow-lg;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
}

.status-monitoring {
  @apply bg-amber-500 shadow-lg;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
}